@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none;
}
html,
body {
  overflow-x: hidden;
}
html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  scroll-behavior: smooth;
  position: relative;
}

:root {
  --background: #eee;
  --color-grey: grey;
  --color-white: #fff;
  --color-black: #000;
  --color-write: #333;
  --primary-signup: orangered;
  --primary-login: #6600ff;
  --primary: #db4c3f;
  --color-light-grey: #ccc;
  --background-color: #fafafa;
  --shadow-color: rgba(0, 0, 0, 0.8);
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.7;
}

.homepage{display:flex;flex-direction:column}

.navbar{display:flex;align-items:center;padding:6px 16%;background-color:var(--primary);position:fixed;width:100vw;height:60px;z-index:1000}.navbar .logo{cursor:pointer}.navbar .logo img{width:45px}.navbar .options{padding:0;margin:0;display:flex;color:var(--color-white);font-size:25px;font-weight:700}.navbar .options__todoadd{cursor:pointer}.navbar .options__signout{margin-left:30px;cursor:pointer}

.main-body{display:flex;flex-wrap:wrap;height:100vh;margin-top:60px}.main-body .space--one{background-color:var(--background-color);width:15%;height:100vh;position:fixed}@media only screen and (max-width: 900px){.main-body .space--one{width:0}}.main-body .space--two{margin-left:85%;background-color:var(--background-color);width:15%;height:100vh;position:fixed}@media only screen and (max-width: 900px){.main-body .space--two{width:0}}

.sidepanel{margin-left:15%;background-color:var(--background-color);width:20%;position:fixed;height:100vh;padding:5px}@media only screen and (max-width: 900px){.sidepanel{width:30%;margin-left:0}}@media only screen and (max-width: 650px){.sidepanel{width:0%;display:none}}.sidepanel__generic li{display:flex;align-items:center;padding:10px;cursor:pointer}.sidepanel__generic li .icon{margin-right:10px;transition:0.3s;transform:rotate(360deg)}.sidepanel__generic li .reverse-icon{transform:rotate(180deg);transition:0.3s}.sidepanel__generic li:hover{background-color:var(--color-white)}.sidepanel__projects{margin-top:10px}.sidepanel__toogleprojects{cursor:pointer;margin-top:10px;padding:5px}.sidepanel__toogleprojects:hover{background-color:var(--color-white)}.sidepanel__toogleprojects p{margin:0;padding:0}.sidepanel__toogleprojects p span{margin-right:5px;color:var(--primary);font-weight:600;font-size:18px}

.addprojects form .input-container{margin-top:10px}.addprojects form input[type="text"]{font-size:14px;font-weight:500;padding:6px 10px;border-radius:5px;outline-color:var(--primary);border:2px solid var(--color-grey);width:90%}.addprojects form input[type="text"]::-webkit-input-placeholder{color:var(--color-grey);font-size:14px;font-weight:400}.addprojects .button{margin-top:10px;display:flex;flex-direction:row}.addprojects .button__add{border:0;outline:none;padding:6px 12px;font-weight:600;color:var(--color-white);border-radius:5px;text-transform:capitalize;background-color:var(--primary)}.addprojects .button__cancel{cursor:pointer;border:0;outline:none;padding:6px 12px;font-weight:600;background-color:transparent;border-radius:5px;text-transform:capitalize}

.list-projects__empty{padding:5px 30px;font-size:1.3rem;font-weight:600}.list-projects__list{height:200px;overflow:hidden;overflow-y:scroll}.list-projects__single{display:flex;align-items:center;justify-content:space-between;padding:5px 30px;text-transform:uppercase;font-weight:600;font-size:1.3rem;color:--primary}.list-projects__single:hover .list-projects__single--trash{display:inline-block}.list-projects__single--dot{font-size:0.8rem;color:var(--primary-login);font-weight:900;margin-right:10px}.list-projects__single--trash{border:none;outline:none;background-color:none;display:none;cursor:pointer}.list-projects__single p{margin:0;padding:0}.list-projects__single:hover{background-color:var(--color-white)}

.addtodo{width:50%;padding:30px;margin-left:35%}@media only screen and (max-width: 900px){.addtodo{width:70%;margin-left:30%}}@media only screen and (max-width: 650px){.addtodo{width:100%;margin-left:0%}}.addtodo h4 span{color:var(--primary);margin-right:10px}.addtodo form{margin-top:30px;width:90%}.addtodo form .input-container{margin-top:10px}.addtodo form input[type="text"],.addtodo form [type="date"]{font-size:14px;font-weight:500;padding:8px 20px;border-radius:5px;outline-color:var(--primary);border:2px solid var(--color-grey);width:100%}.addtodo form input[type="text"]::-webkit-input-placeholder,.addtodo form [type="date"]::-webkit-input-placeholder{color:var(--color-grey);font-size:14px;font-weight:500}.addtodo form select{font-size:14px;font-weight:500;padding:10px 20px;border-radius:5px;outline-color:var(--primary);border:2px solid var(--color-grey);width:100%}.addtodo form .button{margin-top:10px;display:flex;flex-direction:row}.addtodo form .button__add{border:0;outline:none;padding:10px 15px;font-weight:600;color:var(--color-white);border-radius:5px;text-transform:capitalize;background-color:var(--primary)}.addtodo form .button__cancel{cursor:pointer;border:0;outline:none;padding:10px 15px;font-weight:600;background-color:transparent;border-radius:5px;text-transform:capitalize}

.list{width:50%;margin-left:35%;padding:30px}@media only screen and (max-width: 900px){.list{width:70%;margin-left:30%}}@media only screen and (max-width: 650px){.list{width:100%;margin-left:0}}.list .withcolor{padding:5px 20px;display:inline-block;border-radius:5px;background-color:var(--color-white);color:var(--color-write)}.list .withcolor span{color:var(--primary)}.list .withcolor:hover{background-color:var(--primary);color:var(--color-white)}.list .withcolor:hover span{color:var(--color-white)}.list__addtodo{display:flex;align-items:center;margin-top:20px;font-size:1.6rem;color:var(--color-write);cursor:pointer;width:auto;text-transform:capitalize}.list__addtodo span{font-size:20px;margin-right:10px;font-weight:400;color:var(--primary)}.list__todo{padding:20px 0}.list__todo--trash{border:none;outline:none;background-color:none;display:none;cursor:pointer}.list__todo--detail{width:95%;display:flex;justify-content:space-between;align-items:center}.list__todo--single{font-size:1.4rem;color:var(--color-write);text-transform:capitalize}.list__todo--single li{width:100%;display:flex;align-items:center}.list__todo--single li:hover .list-todo--trash{display:inline-block}.list__todo--single li:hover .list__todo--trash{display:inline-block}.list__todo--single li .checkbox{cursor:pointer;height:12px;width:12px;border-radius:50%;position:relative}.list__todo--single li .checkbox::after{content:"";display:block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);border-radius:50%;height:20px;width:20px;border:2px solid var(--color-light-grey)}.list__todo--single li .checkbox-completed{background-color:var(--primary)}.list__todo--single li .task{margin:0;padding:0;margin-left:10px;width:90%}.list__todo--single li .task-completed{color:var(--color-grey);text-decoration:line-through}

.login{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;background-color:var(--background)}.login h2{font-size:26px;font-weight:900;color:var(--primary-login)}.login form{display:flex;flex-direction:column;width:40rem;border:1.5px solid #d9d9d9;border-radius:5px;padding:15px;background-color:var(--color-white);margin-top:10px}@media only screen and (max-width: 650px){.login form{width:30rem;margin:0 20px}}.login form .input-container{width:100%;margin:8px 0;display:flex;outline:none;border:1.5px solid var(--background);border-radius:5px}.login form .icon{font-size:14px;font-weight:400;outline:none;padding:12px 14px;border:none}.login form input[type="text"],.login form input[type="password"],.login form input[type="email"]{font-size:14px;font-weight:500;outline:none;padding-right:10px;border:none;width:100%}.login form input[type="text"]::-webkit-input-placeholder,.login form input[type="password"]::-webkit-input-placeholder,.login form input[type="email"]::-webkit-input-placeholder{color:var(--color-grey);font-size:14px;font-weight:400}.login form .submit-button{margin:5px 0;background-color:var(--primary-login);border:none;padding:10px 34px;outline:none;font-weight:700;color:var(--color-white);border-radius:5px}.login form .loading{margin:5px 0;border:none;padding:10px 34px;outline:none;font-weight:700;color:var(--color-white);border-radius:5px;background-color:var(--primary-login);opacity:0.6}.login .signup-link{margin-top:10px;width:40rem;padding:1rem;font-size:1.4rem;font-weight:500;border:1.5px solid #d9d9d9;border-radius:5px;text-align:center;background-color:var(--color-white)}@media only screen and (max-width: 650px){.login .signup-link{width:30rem;margin:0 20px}}.login .signup-link--link{margin-left:5px}.login .signup-link--link:link,.login .signup-link--link:visited{text-decoration:none;color:var(--primary-login)}.login .signup-link--link:link:hover,.login .signup-link--link:visited:hover{text-decoration:underline}.login .error-link{margin-top:10px;width:40rem;padding:1rem;font-size:1.4rem;font-weight:500;border:1.5px solid #d9d9d9;color:red;border-radius:5px;text-align:center;background-color:var(--color-white)}@media only screen and (max-width: 650px){.login .error-link{width:30rem;margin:0 20px}}

.sign-up{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;background-color:var(--background)}.sign-up h2{font-size:26px;font-weight:900;color:var(--primary-signup)}.sign-up form{display:flex;flex-direction:column;width:40rem;border:1.5px solid #d9d9d9;border-radius:5px;padding:15px;background-color:var(--color-white);margin-top:10px}@media only screen and (max-width: 650px){.sign-up form{width:30rem;margin:0 20px}}.sign-up form .input-container{width:100%;margin:8px 0;display:flex;outline:none;border:1.5px solid var(--background);border-radius:5px}.sign-up form .icon{font-size:14px;font-weight:400;outline:none;padding:12px 14px;border:none}.sign-up form input[type="text"],.sign-up form input[type="password"],.sign-up form input[type="email"]{font-size:14px;font-weight:500;outline:none;padding-right:10px;border:none;width:100%}.sign-up form input[type="text"]::-webkit-input-placeholder,.sign-up form input[type="password"]::-webkit-input-placeholder,.sign-up form input[type="email"]::-webkit-input-placeholder{color:var(--color-grey);font-size:14px;font-weight:400}.sign-up form .submit-button{margin:5px 0;background-color:var(--primary-signup);border:none;padding:10px 34px;outline:none;font-weight:700;color:var(--color-white);border-radius:5px}.sign-up form .loading{margin:5px 0;border:none;padding:10px 34px;outline:none;font-weight:700;color:var(--color-white);border-radius:5px;background-color:var(--primary-signup);opacity:0.6}.sign-up .login-link{margin-top:10px;width:40rem;padding:1rem;font-size:1.4rem;font-weight:500;border:1.5px solid #d9d9d9;border-radius:5px;text-align:center;background-color:var(--color-white)}@media only screen and (max-width: 650px){.sign-up .login-link{width:30rem;margin:0 20px}}.sign-up .login-link--link{margin-left:5px}.sign-up .login-link--link:link,.sign-up .login-link--link:visited{text-decoration:none;color:var(--primary-signup)}.sign-up .login-link--link:link:hover,.sign-up .login-link--link:visited:hover{text-decoration:underline}.sign-up .error-link{margin-top:10px;width:40rem;padding:1rem;font-size:1.4rem;font-weight:500;border:1.5px solid #d9d9d9;color:red;border-radius:5px;text-align:center;background-color:var(--color-white)}@media only screen and (max-width: 650px){.sign-up .error-link{width:30rem;margin:0 20px}}

.spinner-box{display:flex;position:fixed;z-index:200000;width:100vw;height:100vh;top:0;align-items:center;justify-content:center;height:100vh;background-color:var(--primary-login);background-image:linear-gradient(315deg, var(--primary-login) 0%, var(--primary-signup) 74%)}.spinner{width:70px;text-align:center}.spinner>div{width:18px;height:18px;background-color:var(--color-white);border-radius:100%;display:inline-block;-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both}.spinner .bounce1{-webkit-animation-delay:-0.32s;animation-delay:-0.32s}.spinner .bounce2{-webkit-animation-delay:-0.16s;animation-delay:-0.16s}@-webkit-keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,100%{transform:scale(0)}40%{transform:scale(1)}}

